/* Settings
------------------------------------------------------------------ */
/* Global
------------------------------------------------------------------ */
html {
  box-sizing: border-box;
  font-size: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  background: white;
  font-family: Helvetica, Arial, sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic; }

textarea {
  height: auto;
  min-height: 50px;
  border-radius: 3px; }

select {
  width: 100%;
  border-radius: 3px; }

/* [Core] - Breakpoints
------------------------------------------------------------------ */
/* [Core] - Helpers
------------------------------------------------------------------ */
.container--fluid {
  margin-left: auto;
  margin-right: auto; }

.container--limitations {
  max-width: 75rem; }

.clearfix, .row {
  *zoom: 1; }
  .clearfix::before, .row::before, .clearfix::after, .row::after {
    display: table;
    content: ''; }
  .clearfix::after, .row::after {
    clear: both; }

/* [Core] - Typography
------------------------------------------------------------------ */
h1, h2, h3, h4, h5, h6 {
  font-family: "Times New Roman", serif;
  font-style: normal;
  text-rendering: optimizeLegibility; }

h1 {
  font-size: 1.875rem; }

h2 {
  font-size: 1.625rem; }

h3 {
  font-size: 1.5rem; }

h4 {
  font-size: 1.25rem; }

h5 {
