h6 {
  font-size: 1rem; }

p {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: inherit;
  line-height: 1.6;
  text-rendering: optimizeLegibility; }

a {
  line-height: inherit;
  color: #2c87c2;
  text-decoration: underline;
  cursor: pointer; }
  a:hover, a:focus {
    color: #4b9fd6;
    text-decoration: none; }
  a img {
    border: 0; }

ul,
ol,
dl {
  margin-bottom: 1rem;
  list-style-position: outside;
  line-height: 1.6; }

li {
  font-size: inherit; }

ul {
  list-style-type: disc; }

